@import-normalize;.carousel-header .carousel-item::before {
	filter: none;
}

.carousel-header .carousel-item::after {
	background: none;
}

.carousel-header .carousel-control-next .carousel-control-next-icon {
	display: none;
}

.carousel-header .main-header h2 {
	padding-left: 15px;
	background-color: #6c0;
	box-decoration-break: clone;
	color: #FFF;
	display: inline;
	font-size: 1.7rem;
	line-height: 4.5rem;
	text-transform: uppercase;
	padding: 10px 1em;
}

.carousel-header .main-header h3 {
	color: #212529;
	display: block;
	font-size: 1.6rem;
	line-height: 2.5rem;
	text-transform: uppercase;
	box-decoration-break: clone;
	padding: 0 18px;
	background: #ffffffcc;
	width: auto;
	margin: 10px 0 20px;
	max-width: fit-content;
}

.carousel-indicators {
	display: none;
}

.carousel-preview-wrap {
	display: none;
}

.country-dropdown-toggle,
.country-dropdown-toggle:focus,
.country-dropdown-toggle:hover,
.top-navigation a {
	color: grey;
}

.country-dropdown-toggle:focus,
.country-dropdown-toggle:hover {
	color: #6c0;
}

.country-dropdown-toggle:after:focus,
.country-dropdown-toggle:after:hover {
	background-image: url(images/down-arrow-green.svg) !important;
}

#country-list > ul > li > ul > li > a {
	color: #fff;
}

#country-list > ul > li > ul > li > a:hover {
	color: #8bcc49;
}

.navbar-dropdown .active .nav-link,
.navbar-dropdown .nav-link:focus,
.navbar-dropdown .nav-link:hover {
	border-color: #6c0;
	color: #212529;
}

.dropdown:hover > .dropdown-menu {
	max-height: 500px;
	opacity: 1;
}

.dropdown .dropdown-menu {
	transition: all 0.3s;
	max-height: 0;
	display: block;
	overflow: hidden;
	opacity: 0;
	min-width: 100%;
}

.dropdown-toggle::after {
	transform: rotate(90deg);
	transition: 0.3s;
}

.dropdown-toggle:hover:after {
	transform: rotate(0deg);
	transition: 0.3s;
}

.dropdown-item:hover {
	border-bottom: 2px solid #6c0;
}

.dropdown-item {
	border-bottom: 2px solid white;
}

#donate-button-wrapper {
	padding-left: 0;
}

.carousel-header .carousel-control-next {
	display: none;
}

.btn-navbar-toggle,
.country-dropdown-toggle,
.nav-item,
.nav-link,
.navbar-search-toggle,
.site-logo {
	font-weight: 500;
}

.country-dropdown-toggle {
