@media (min-width: 576px) {

.page-parent .skewed-overlay:before,
.tag .skewed-overlay:before {
	background: transparent;
	background: linear-gradient(245deg, transparent 11%, rgba(255, 255, 255, 0.2) 17%, rgba(255, 255, 255, 0.9) 28%, white 51%, rgba(255, 255, 255, 0.85) 58%, rgba(255, 255, 255, 0.3) 72%, rgba(255, 255, 255, 0.9) 90%, rgba(255, 255, 255, 0.9) 90%, rgba(255, 255, 255, 0.95) 90%);
}

.page-child .skewed-overlay:before {
