	background: linear-gradient(245deg, transparent 25%, rgba(255, 255, 255, 0.2) 28%, rgba(255, 255, 255, 0.9) 44%, rgba(255, 255, 255, 0.7) 66%, rgba(255, 255, 255, 0.85) 45%, rgba(255, 255, 255, 0.3) 64%, rgba(255, 255, 255, 0.5) 65%, rgba(255, 255, 255, 0.9) 95%, rgba(255, 255, 255, 0.9) 100%);
}

}

@media (min-width: 768px) {

.page-id-7408 > .page-header .container > *:not(.row) {
