}

.navbar-dropdown {
	box-sizing: border-box;
	flex-direction: column;
	justify-content: space-between;
	position: absolute;
	top: 0;
	left: 0;
	overflow-y: auto;
	overflow-x: hidden;
	background: url("images/dropdown-gradient.svg") no-repeat #FFF;
}

}

